"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, LogOut, Activity, 
  Sparkles, UserCheck, ShieldCheck, Zap, Send, ChevronLeft, ChevronRight, Loader2, Trophy, BarChart3, Users
} from "lucide-react"

// --- 인터페이스 정의 ---
interface Promotion {
  id: string; 
  name: string;
  pointAmount: number;
  totalStock: number;
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
}

interface PromotionStatus {
  queueSize: number;
  participantCount: number;
  winnerCount: number;
  remainingStock: number;
}

interface Winner {
  userId: string;
  userName: string;
  winningOrder: number;
  wonAt: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  
  const BASE_URL = "http://34.50.7.8:3000/v1"
  const PROMO_API = `${BASE_URL}/promotions`

  // 상태 관리
  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)
  
  // 모달 제어 상태
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)
  const [isStatusModalOpen, setIsStatusModalOpen] = useState(false)
  const [isWinnerModalOpen, setIsWinnerModalOpen] = useState(false)
  
  // 상세 데이터 상태
  const [currentStatus, setCurrentStatus] = useState<PromotionStatus | null>(null)
  const [winners, setWinners] = useState<Winner[]>([])
  const [selectedPromoName, setSelectedPromoName] = useState("")

  // 1. 목록 조회 (GET /v1/promotions)
  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=5`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content)
        setTotalPromoPages(data.result.totalPages)
      }
    } catch (error) {
      console.error("Fetch Error:", error)
    } finally {
      setIsLoading(false)
    }
  }, [promoPage, PROMO_API])

  // 2. 당첨자 목록 조회 (GET /v1/promotions/{id}/winners) - 추가됨
  const fetchWinners = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${PROMO_API}/${id}/winners`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setWinners(data.result)
        setSelectedPromoName(name)
        setIsWinnerModalOpen(true)
      }
    } catch (e) {
      alert("당첨자 목록을 불러올 수 없습니다.")
    }
  }

  // 3. 참여 현황 조회 (GET /v1/promotions/{id}/status)
  const fetchStatus = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${PROMO_API}/${id}/status`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setCurrentStatus(data.result)
        setSelectedPromoName(name)
        setIsStatusModalOpen(true)
      }
    } catch (e) { alert("상태 조회 실패") }
  }

  // 4. 시작/종료 (PATCH /start, /end)
  const toggleStatus = async (id: string, action: 'start' | 'end') => {
    const token = localStorage.getItem("accessToken")
    if (!confirm(`프로모션을 ${action === 'start' ? '시작' : '종료'}하시겠습니까?`)) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
      else alert(data.message)
    } catch (e) { alert("상태 변경 실패") }
  }

  // 5. 생성 (POST /v1/promotions)
  const [newPromo, setNewPromo] = useState({ name: "", pointAmount: "", totalStock: "", startTime: "", endTime: "" })
  const handleCreate = async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(PROMO_API, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({
          ...newPromo,
          pointAmount: Number(newPromo.pointAmount),
          totalStock: Number(newPromo.totalStock),
          startTime: newPromo.startTime + ":00",
          endTime: newPromo.endTime + ":00"
        })
      })
      const data = await res.json()
      if (data.isSuccess) {
        setIsPromoCreateOpen(false)
        fetchPromotions()
      }
    } catch (e) { alert("생성 에러") }
  }

  useEffect(() => {
    setIsMounted(true)
    fetchPromotions()
  }, [fetchPromotions])

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-[#F4F7FE] text-slate-900 font-sans">
      <header className="border-b bg-white/70 backdrop-blur-lg sticky top-0 z-50 px-8 h-16 flex items-center justify-between">
        <Image src="/logo.png" alt="Logo" width={90} height={30} />
        <Button variant="ghost" size="sm" onClick={() => router.push('/login')} className="font-bold text-red-500 hover:bg-red-50">
          <LogOut className="h-4 w-4 mr-2" /> LOGOUT
        </Button>
      </header>

      <main className="container mx-auto py-10 px-6">
        <div className="flex justify-between items-end mb-10">
          <div>
            <h1 className="text-4xl font-black italic tracking-tighter uppercase mb-2">Management</h1>
            <p className="text-slate-500 font-medium">실시간 프로모션 및 당첨자 제어 센터</p>
          </div>
          <Button onClick={() => setIsPromoCreateOpen(true)} className="rounded-2xl h-12 px-8 font-bold shadow-xl shadow-primary/20">
            <Plus className="mr-2 h-5 w-5" /> 프로모션 등록
          </Button>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          <Card className="lg:col-span-2 p-8 rounded-[2rem] border-none shadow-sm bg-white">
            <div className="flex items-center justify-between mb-8">
              <h2 className="text-xl font-bold flex items-center gap-2">
                <Activity className="h-5 w-5 text-primary" /> Active Campaigns
              </h2>
            </div>
            
            <div className="space-y-4">
              {isLoading ? (
                <div className="flex justify-center py-20"><Loader2 className="animate-spin text-primary" /></div>
              ) : (
                promotions.map((p) => (
                  <div key={p.id} className="p-5 rounded-[1.5rem] border border-slate-50 bg-slate-50/50 hover:bg-white hover:shadow-md hover:border-primary/10 transition-all flex items-center justify-between">
                    <div>
                      <div className="flex items-center gap-2 mb-1">
                        <span className="font-black text-lg tracking-tight">{p.name}</span>
                        <Badge className={`text-[9px] font-black ${p.status === 'ACTIVE' ? 'bg-green-500' : 'bg-slate-400'}`}>
                          {p.status}
                        </Badge>
                      </div>
                      <p className="text-xs text-slate-400 font-bold uppercase tracking-wider">
                        {p.pointAmount.toLocaleString()} PTS · STOCK: {p.totalStock}
                      </p>
                    </div>

                    <div className="flex items-center gap-2">
                      {/* 당첨자 목록 버튼 (추가된 부분) */}
                      <Button variant="outline" size="icon" className="rounded-xl h-10 w-10 border-yellow-200 hover:bg-yellow-50" onClick={() => fetchWinners(p.id, p.name)}>
                        <Trophy className="h-4 w-4 text-yellow-600" />
                      </Button>
                      
                      <Button variant="outline" size="icon" className="rounded-xl h-10 w-10 border-blue-200 hover:bg-blue-50" onClick={() => fetchStatus(p.id, p.name)}>
                        <BarChart3 className="h-4 w-4 text-blue-600" />
                      </Button>
                      
                      {p.status === "SCHEDULED" ? (
                        <Button variant="outline" size="icon" className="rounded-xl h-10 w-10 border-green-200" onClick={() => toggleStatus(p.id, 'start')}>
                          <Play className="h-4 w-4 text-green-600 fill-current" />
                        </Button>
                      ) : p.status === "ACTIVE" ? (
                        <Button variant="outline" size="icon" className="rounded-xl h-10 w-10 border-red-200" onClick={() => toggleStatus(p.id, 'end')}>
                          <Square className="h-4 w-4 text-red-600 fill-current" />
                        </Button>
                      ) : null}
                    </div>
                  </div>
                ))
              )}
            </div>

            <div className="flex justify-center items-center gap-6 mt-10">
              <Button variant="ghost" disabled={promoPage === 0} onClick={() => setPromoPage(p => p - 1)} className="rounded-full"><ChevronLeft /></Button>
              <span className="font-black text-sm italic">{promoPage + 1} / {totalPromoPages}</span>
              <Button variant="ghost" disabled={promoPage >= totalPromoPages - 1} onClick={() => setPromoPage(p => p + 1)} className="rounded-full"><ChevronRight /></Button>
            </div>
          </Card>

          <div className="space-y-6">
            <Card className="p-8 rounded-[2rem] border-none shadow-xl bg-primary text-white relative overflow-hidden">
              <Zap className="absolute -right-4 -bottom-4 h-32 w-32 opacity-10" />
              <h3 className="text-lg font-bold mb-4 flex items-center gap-2"><ShieldCheck /> 관리 권한</h3>
              <p className="text-sm opacity-80 leading-relaxed">
                현재 매니저 권한으로 접속 중입니다. 프로모션 생성, 상태 변경 및 당첨자 명단 추출이 가능합니다.
              </p>
            </Card>
            
            <Card className="p-8 rounded-[2rem] border-none shadow-sm bg-white">
              <h3 className="text-lg font-bold mb-4 flex items-center gap-2"><Users className="text-primary" /> 시스템 현황</h3>
              <div className="space-y-4">
                <div className="flex justify-between items-center text-sm">
                  <span className="text-slate-400 font-medium">연결 포트</span>
                  <span className="font-bold">3000 (API)</span>
                </div>
                <div className="flex justify-between items-center text-sm">
                  <span className="text-slate-400 font-medium">상태</span>
                  <Badge className="bg-green-100 text-green-600 border-none">정상</Badge>
                </div>
              </div>
            </Card>
          </div>
        </div>
      </main>

      {/* 모달: 당첨자 목록 (GET /winners 적용) */}
      <Dialog open={isWinnerModalOpen} onOpenChange={setIsWinnerModalOpen}>
        <DialogContent className="rounded-[2rem] sm:max-w-md border-none">
          <DialogHeader>
            <div className="h-12 w-12 bg-yellow-100 rounded-2xl flex items-center justify-center mb-4">
              <Trophy className="h-6 w-6 text-yellow-600" />
            </div>
            <DialogTitle className="text-2xl font-black italic uppercase tracking-tighter">Winner List</DialogTitle>
            <p className="text-sm text-slate-500">{selectedPromoName}</p>
          </DialogHeader>
          <div className="max-h-[350px] overflow-y-auto pr-2 space-y-2 my-4">
            {winners.length > 0 ? winners.map((w) => (
              <div key={w.userId} className="flex justify-between items-center p-4 bg-slate-50 rounded-2xl border border-slate-100">
                <div>
                  <p className="font-bold text-sm">{w.userName}</p>
                  <p className="text-[10px] text-slate-400 font-mono">{new Date(w.wonAt).toLocaleString()}</p>
                </div>
                <Badge className="bg-primary h-6 rounded-lg font-black italic">{w.winningOrder}등</Badge>
              </div>
            )) : (
              <div className="text-center py-10 opacity-30 italic text-sm">당첨 데이터가 없습니다.</div>
            )}
          </div>
          <Button onClick={() => setIsWinnerModalOpen(false)} className="w-full h-12 rounded-xl font-bold">확인</Button>
        </DialogContent>
      </Dialog>

      {/* 모달: 참여 현황 */}
      <Dialog open={isStatusModalOpen} onOpenChange={setIsStatusModalOpen}>
        <DialogContent className="rounded-[2rem] sm:max-w-md border-none">
          <DialogHeader>
            <DialogTitle className="text-2xl font-black italic uppercase tracking-tighter">Live Participation</DialogTitle>
            <p className="text-sm text-slate-500">{selectedPromoName}</p>
          </DialogHeader>
          {currentStatus && (
            <div className="grid grid-cols-2 gap-3 py-4">
              <div className="p-5 bg-blue-50 rounded-[1.5rem] border border-blue-100 text-center">
                <p className="text-[9px] font-black uppercase text-blue-400 mb-1">Queue Size</p>
                <p className="text-2xl font-black text-blue-700">{currentStatus.queueSize}</p>
              </div>
              <div className="p-5 bg-slate-50 rounded-[1.5rem] border border-slate-100 text-center">
                <p className="text-[9px] font-black uppercase text-slate-400 mb-1">Total Join</p>
                <p className="text-2xl font-black">{currentStatus.participantCount}</p>
              </div>
              <div className="p-5 bg-primary/5 rounded-[1.5rem] border border-primary/10 text-center">
                <p className="text-[9px] font-black uppercase text-primary/60 mb-1">Total Winners</p>
                <p className="text-2xl font-black text-primary">{currentStatus.winnerCount}</p>
              </div>
              <div className="p-5 bg-slate-50 rounded-[1.5rem] border border-slate-100 text-center">
                <p className="text-[9px] font-black uppercase text-slate-400 mb-1">Left Stock</p>
                <p className="text-2xl font-black">{currentStatus.remainingStock}</p>
              </div>
            </div>
          )}
          <Button onClick={() => setIsStatusModalOpen(false)} className="w-full h-12 rounded-xl font-bold">닫기</Button>
        </DialogContent>
      </Dialog>

      {/* 모달: 프로모션 생성 */}
      <Dialog open={isPromoCreateOpen} onOpenChange={setIsPromoCreateOpen}>
        <DialogContent className="rounded-[2rem] border-none">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-xl">New Campaign</DialogTitle></DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label className="text-[10px] font-black uppercase text-slate-400 ml-1">Event Name</Label>
              <Input className="rounded-xl bg-slate-50 border-none h-12 px-4 font-medium" value={newPromo.name} onChange={e => setNewPromo({...newPromo, name: e.target.value})} />
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label className="text-[10px] font-black uppercase text-slate-400 ml-1">Reward Points</Label>
                <Input type="number" className="rounded-xl bg-slate-50 border-none h-12" value={newPromo.pointAmount} onChange={e => setNewPromo({...newPromo, pointAmount: e.target.value})} />
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-black uppercase text-slate-400 ml-1">Max Winners</Label>
                <Input type="number" className="rounded-xl bg-slate-50 border-none h-12" value={newPromo.totalStock} onChange={e => setNewPromo({...newPromo, totalStock: e.target.value})} />
              </div>
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label className="text-[10px] font-black uppercase text-slate-400 ml-1">Start Time</Label>
                <Input type="datetime-local" className="rounded-xl bg-slate-50 border-none h-12 text-xs" value={newPromo.startTime} onChange={e => setNewPromo({...newPromo, startTime: e.target.value})} />
              </div>
              <div className="space-y-2">
                <Label className="text-[10px] font-black uppercase text-slate-400 ml-1">End Time</Label>
                <Input type="datetime-local" className="rounded-xl bg-slate-50 border-none h-12 text-xs" value={newPromo.endTime} onChange={e => setNewPromo({...newPromo, endTime: e.target.value})} />
              </div>
            </div>
          </div>
          <DialogFooter><Button onClick={handleCreate} className="w-full h-14 rounded-2xl font-black uppercase tracking-widest">Deploy Campaign</Button></DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}